                            <div class="row-b__col elem-pos-fix" style="width: 257px; min-width: 257px; " :class="{'--active':form.other.focus}">
                                <div class="mtb construct-form__big-note">
                                    <div class="construct-form__note mt16">Если вам не хватает стандартных блоков &mdash; добавьте<br> свой. Только не забудьте придумать ему название.</div>
                                </div>
                            </div>
                        </div>
                        <construct-media-add :open="true"
                                             :move-to-list="moveTo"
                                             :name-title="otherTitle"
                                             :value="form.otherMedia.value"
                                             :name="form.otherMedia.name"
                                             @input="inputText($event,form.otherMedia.name)"
                                             @move="movePhoto($event,form.otherMedia.name)"
                                             :is-moderaite="isModerator"
                                             class="mt8"></construct-media-add>

                        <!-- end row-b -->
                    </construct-check-show>
                </div>



                <!--<div class="wrapper-content wrapper-content-inner mt40" :id="'scroll-'+form.resume.name">
                    <div class="construct-form__sect-widget">
                        <div class="construct-form__check-title">
                            <input type="checkbox" name="" id="construct-action-resume" checked disabled>
                            <label-hint label="Призыв к действию" for-input="construct-action-resume"></label-hint>
                            <span class="note">(Покупатели не видят название этого блока, только текст...)</span>
                        </div>
                    </div>

                    <div class="row-b mt24">
                        <div class="row-b__col elem-pos-fit">
                            <div class="full">
                                <textarea-field :is-error="form.resume.error"
                                                :value="form.resume.value"
                                                :placeholder="'Например, «Оформляйте наши купоны, обращайтесь к нам, называйте кодовое слово «ЗЕМСКИДКИ» и получайте эксклюзивные скидки, а также особые привилегии и бонусы!»'"
                                                @focus="focus(form.resume.name)"
                                                @blur="blur(form.resume.name)"
                                                class="full"
                                                @input="inputText($event,form.resume.name)"
                                                :add-style="{minHeight: 162 + 'px'}">
                                </textarea-field>
                            </div>
                        </div>

                        <div class="row-b__col elem-pos-fix" style="width: 257px; min-width: 257px; " :class="{'&#45;&#45;active':form.resume.focus}">
                            <div class="mtb">
                                <div class="mt16 construct-form__note">Эффектно завершите рассказ о себе, а также<br> призовите покупателя к конкретным действиям,<br> например, «Приходите к нам!»</div>
                            </div>
                        </div>
                    </div>

                    &lt;!&ndash;<construct-media-add :open="true"
                    :move-to-list="moveTo"
                                         :name-title="'Завершающий текст'"
                                         :value="form.resumeMedia.value"
                                         :name="form.resumeMedia.name"
                                         @input="inputText($event,form.resumeMedia.name)"
                                         :is-moderaite="isModerator"
                                         class="mt8"></construct-media-add>&ndash;&gt;
                    &lt;!&ndash; end row-b &ndash;&gt;
                </div>-->
                <!-- SIGN -->
                <div class="wrapper-content wrapper-content-inner mt40" :id="'scroll-'+form.sign.name">
                    <div class="construct-form__sect-widget">
                        <div class="construct-form__check-title">
                            <input type="checkbox" name="" id="construct-action-resume" checked disabled>
                            <label-hint label="Подпись руководителя компании" for-input="construct-action-sign"></label-hint>
                            <span class="note">(Покупатели не видят название этого блока, только текст...)</span>
                        </div>
                    </div>

                    <div class="row-b mt24">
                        <div class="row-b__col elem-pos-fit">
                            <div class="full">
                                <textarea-field :is-error="form.sign.error"
                                                :value="form.sign.value"
                                                :placeholder="'Например, «С уважением, Руководитель компании «ААА», Иванов Иван Иванович»'"
                                                @focus="focus(form.sign.name)"
                                                @blur="blur(form.sign.name)"
                                                class="full"
                                                @input="inputText($event,form.sign.name)"
                                                :add-style="{minHeight: 162 + 'px'}">
                                </textarea-field>
                            </div>
                        </div>

                        <div class="row-b__col elem-pos-fix" style="width: 257px; min-width: 257px; " :class="{'--active':form.sign.focus}">
                            <div class="mtb construct-form__big-note">
                                <div class="mt16 construct-form__note">В этом блоке разместите подпись, <br>составленную от лица руководителя компании...</div>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- end wrapper-content -->

            </div>
            <!-- end container__full -->




        </section>
        <!-- end container -->


        <popup :is-set-styled="true" :show="popups.partnerEditAboutWelcome.show" :name="popups.partnerEditAboutWelcome.name">
            <partner-edit-about-welcome></partner-edit-about-welcome>
        </popup>

    </div>
</template>



<script>
    import Popup from '@/components/modal/Popup.vue';
    import {mapState} from 'vuex';
    import PartnerEditAboutWelcome from '@/components/modal/PartnerEditAboutWelcome.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import InputStyled from '@/components/forms/InputStyled.vue';
    import TextEditorMax from '@/components/editor/TextEditorMax.vue';
    import WelcomePartner from '@/components/modal/WelcomePartner.vue'
    import ConstructPartnerFinal from '@/components/modal/ConstructPartnerFinal.vue'
    import ConstructItemShow from '@/components/ConstructItemShow.vue'
    import FileUploadCropper from "@/components/forms/LogoFileUploadCropper.vue";
    import ConstructMediaAdd from '@/components/forms/ConstructMediaAdd.vue'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import ConstructCheckShow from '@/components/ConstructCheckShow.vue'
    import LabelHint from '@/components/forms/LabelHint.vue'

    export default {
        name: "PartnerAboutEditor",
        components: {
            Popup,
            PartnerEditAboutWelcome,
            InputStyled,
            Tooltip,
            TextEditorMax,
            WelcomePartner,
            ConstructPartnerFinal,
            ConstructItemShow,
            FileUploadCropper,
            ConstructMediaAdd,
            ConstructCheckShow,
            TextareaField,
            LabelHint
        },
        props: {
            isModerator: {
                default: false
            },
        },
        data: function() {
            return {
                init: false,
                moveTo: {
                    aboutTextMedia:{
                        name: 'О компании',
                        code: 'aboutTextMedia'
                    },
                    advantagesMedia:{
                        name: 'Наши преимущества',
                        code: 'advantagesMedia'
                    },
                    portfolioMedia:{
                        name: 'Портфолио',
                        code: 'portfolioMedia'
                    },
                    collectiveMedia:{
                        name: 'Наш коллектив',
                        code: 'collectiveMedia'
                    },
                    equipmentMedia:{
                        name: 'Инструменты и оборудование',
                        code: 'equipmentMedia'
                    },
                    shopsMedia:{
                        name: 'Магазины и шоурумы',
                        code: 'shopsMedia'
                    },
                    productionMedia:{
                        name: 'Производство',
                        code: 'productionMedia'
                    },
                    otherMedia:{
                        name: 'Другое',
                        code: 'otherMedia'
                    },
                }
            }
        },
        mounted: function () {
            //this.openPartnerEditAboutWelcome();
            if(this.partner.id > 0) {
                this.init = true;
                this.moveTo.otherMedia.name = this.otherTitle
            }
        },
        computed: {
            logo: {
                get () {
                    return this.form.logo.value
                },
                set (value) {
                    this.$store.commit('partner/setAboutForm', {
                        name: 'logo',
                        value:value,
                    })
                }
            },
            name: {
                get () {
                    return this.form.name.value
                },
                set (value) {
                    this.$store.commit('partner/setAboutForm', {
                        name: 'name',
                        value:value,
                    })
                }
            },
            site: {
                get () {
                    return this.form.site.value
                },
                set (value) {
                    this.$store.commit('partner/setAboutForm', {
                        name: 'site',
                        value:value,
                    })
                }
            },
            otherTitle: {
                get () {
                    return this.form.otherTitle.value
                },
                set (value) {
                    this.$store.commit('partner/setAboutForm', {
                        name: 'otherTitle',
                        value:value,
                    });
                    this.moveTo.otherMedia.name = value
                }
            },

            ...mapState({
                popups: state => state.popups.items,
                form: state => state.partner.aboutForm,
                partner: state => state.partner.current,
            }),
        },
        methods: {
            hideOther(){
                this.$store.commit('partner/setAboutFormOpen',{ name: 'other', value: false });
                this.$refs.otherProviderContarct.openItem();
            },
            openPartnerEditAboutWelcome: function () {
                this.$store.dispatch('popups/open', {name: this.popups.partnerEditAboutWelcome.name});
            },
            setError (field,value) {
                this.$store.commit('partner/setAboutFormError',{ name: field, value: false })
                setTimeout(()=>{
                    this.$store.commit('partner/setAboutFormError',{ name: field, value: value })
                },150);
            },
            setRequired (value,field) {
                this.$store.commit('partner/setAboutFormRequired',{ name: field, value: value })
                if(field === 'other'){
                    this.$store.commit('partner/setAboutFormRequired',{ name: 'otherTitle', value: value })
                    this.$store.commit('partner/setAboutFormOpen',{ name: 'other', value: value })
                } else {
                    this.$store.commit('partner/setAboutFormOpen',{ name: field, value: value });
                }
                /*if(!value){
                    this.$store.commit('partner/setAboutForm', {
                        name: field,
                        value:'',
                    })
                    if(field === 'other'){
                        this.$store.commit('partner/setAboutForm',{ name: 'otherTitle', value: '' })
                    }
                }*/
            },
            setOpen(value,field){
                if(value === true && this.$refs[field] && field !== 'other'){
                    this.$nextTick(() => {this.$refs[field].$el.querySelector('textarea').focus()});
                }
                if(value === true && field === 'other'){
                    this.$store.commit('partner/setAboutFormOpen',{ name: 'other', value: value });
                    this.$nextTick(() => {this.$refs['otherTitle'].$el.querySelector('input').focus()})
                } else {
                    this.$store.commit('partner/setAboutFormOpen',{ name: field, value: value });
                }
            },
            focus(field){
                this.$store.commit('partner/setAboutFormFocus',{ name: field, value: true });
                this.removeError(field);
            },
            blur(field){
                this.$store.commit('partner/setAboutFormFocus',{ name: field, value: false })
            },
            removeError (field) {
                this.$store.commit('partner/setAboutFormError',{ name: field, value: false })
            },
            inputText(value,name){
                this.$store.commit('partner/setAboutForm', {
                    name: name,
                    value:value,
                })
            },
            movePhoto(value,name){
                this.$store.commit('partner/setAboutForm', {
                    name: name,
                    value:{
                        photos: value.files,
                        videos: [...this.form[name].value.videos],
                    },
                })
                let media = {
                    photos: [...this.form[value.move.to].value.photos],
                    videos: [...this.form[value.move.to].value.videos],
                };
                media.photos.push(value.move.file);
                this.$store.commit('partner/setAboutForm', {
                    name: value.move.to,
                    value: media,
                })
                let toParent = value.move.to.replace('Media','');
                if(this.form[toParent].required == false){
                    this.$refs[toParent+'ProviderContarct'].openItem();
                }
            },
            pageUp(){
                setTimeout(()=>{
                    window.scroll(0,0);
                    this.init = true;
                },200)
            },
        },
    }
</script>
