<template>
    <div :class="{open:opened}" style="position: relative; ">
        <div class="construct-form__wrap-title h2 mb0">{{ propTitle }}</div>
        
        <div class="construct-form__wrap-open" @click="openItem">
            <span v-if="opened">Убрать</span>
            <span v-else>Добавить</span>
        </div>
        
        <transition
                @enter="enterSlideUp"
                @leave="leaveSlideUp"
                :css="false">
            <div v-show="opened" class="wrap-hidden">
                <slot></slot>
            </div>
        </transition>
    </div>
    <!-- end item -->
</template>

<script>
    import Velocity from 'velocity-animate';

    export default {
        name: "ConstructItemShow",
        props: {
            open: {default: false},
            propTitle: {default: ''}
        },

        data: function () {
            return {
                opened: false,
            }
        },

        computed: {},

        created: function () {
            this.$set(this, 'opened', !!this.open);
            this.$emit('opened', this.opened);
        },

        mounted: function () {

        },

        methods: {
            openItem(){
                this.opened = !this.opened;
                this.$emit('opened', this.opened);
            },
            enterSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideDown", { duration: 300 }).then(function(e) { done(); });
            },

            leaveSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideUp", { duration: 300 }).then(function(e) { done(); });
            },
        }
    }
</script>

