<template>
    <text-editor
            v-model="valueEditor"
            :ref="refer"
            :editor-toolbar="toolbarSettings"
            :hide-toolbar="hideToolbar"
            :editor-options="editorSettings"
            :template-default="templateDefault"
            :placeholder-html="placeholderHtml"
            :set-target-blank="setTargetBlank"
            :class="{'error-editor':error,'hide-toolbar':hideToolbar, 'editor-focus': focus}"
            :placeholder="placeholder"
            @focus="onEditorFocus"
            @blur="onEditorBlur"
            @ready="ready"
            @set_value="setValue"
            @selection-change="onSelectionChange">
    </text-editor>
</template>

<script>
    import TextEditor from '@/components/editor/TextEditor.vue';
    import axios from 'axios'
    import {jsonHeader} from '@/helpers/axiosHeaders'
    import qs from "qs";

    export default {
        name: "TextEditorMax",
        components: {
            TextEditor
        },
        props: {
            id: {
                type: String,
                default: "quill-container"
            },
            placeholder: {
                type: String,
                default: ""
            },
            placeholderHtml: {
                type: String,
                default: ""
            },
            templateDefault: {
                type: String,
                default: ""
            },
            refer: {
                type: String,
                default: ""
            },
            valueDefault: {
                type: String,
                default: ""
            },
            hideToolbar: {
                type: Boolean,
                default: false
            },
            setTargetBlank: {
                type: Boolean,
                default: false
            },
            error: {
                type: Boolean,
                default: false
            },
            showBtnMax: {
                type: Boolean,
                default: false
            },
            disabled: {
                type: Boolean
            },
        },

        data: function() {
            return {
                toolbarSettings: [
                    ["bold"],
                    [{ list: "bullet" }],
                    ["video"],
                    ['gallery']
                ],
                gallery: {},
                editorSettings: {},
                valueEditor: '',
                focus: false,
            };
        },

        mounted: function() {

            this.$nextTick(function () {
                let self = this;
                let found = this.valueDefault.match(/#GALLERY_(\d+)#/g);
                let galleries = [];
                for(let index in found){
                    galleries.push(found[index].match(/(\d+)/g)[0]);
                }
                if(galleries.length > 0){
                    axios.get(process.env.VUE_APP_API_PATH+'/user/galleries/',{
                        headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                        params: {
                            galleries: galleries,
                        },
                        paramsSerializer: function(params) {
                            return qs.stringify(params, {arrayFormat: 'indices'})
                        },
                        withCredentials: true,

                    }).then(response => {
                        if(typeof response.data === 'object' && +response.data.error === 0) {
                            if (found !== null) {
                                self.valueEditor = self.valueDefault.replace(/#GALLERY_(\d+)#/g, function(str, ind)
                                {
                                    response.data.result.items[+ind].replace(/[\n\r]/g, '').replace('"','&quot;');
                                    return '<iframe srcdoc="<section id=&quot;surrogate_gallery_'+ind+'&quot;>' +
                                        '<style>@import url(/css/img_thumbnails.css?v125);</style>' +
                                        response.data.result.items[+ind] +
                                        '</section>" allowfullscreen="true" width="100%" frameborder="0"></iframe>';

                                });
                                self.valueEditor = self.valueEditor.replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');
                            }
                            else
                            {
                                self.valueEditor = self.valueDefault.replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');

                            }
                        } else {
                            if( +response.data.error===401 ){
                                this.$store.dispatch('auth/logout')
                            }
                        }
                    }).catch(function (error) {
                        self.valueEditor = self.valueDefault.replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');
                    });
                }
                else
                {
                    self.valueEditor = self.valueDefault.replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');

                }

            });

        },

        methods: {
            onEditorBlur: function(quill) {
                this.$nextTick(function () {
                    //this.$emit("error", false);
                    this.focus = false;
                });
                this.$emit("blur");
            },
            onEditorFocus: function(quill) {
                this.$nextTick(function () {
                    this.$emit("error", false);
                    this.$emit("focus", false);
                    this.focus = false;
                });
            },
            onEditorReady: function(quill) {
            },
            onEditorChange: function(quill, html, text) {
            },
            onSelectionChange: function(range, oldRange, source) {
            },
            ready(quill){
                this.$emit("ready",quill);
            },
            setValue(quill){
                this.$emit("set_value",quill);
            }
        },

        watch: {
            valueEditor: function () {
                let newValue = this.valueEditor.replace(/<iframe srcdoc="<section[\s\S]*?id=&quot;surrogate_gallery_(\d+)[\s\S]*?\/iframe>/gi, function(str, ind)
                {
                    return '#GALLERY_'+ind+'#';

                });
                newValue = newValue.replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');
                this.$emit("input", newValue);
                this.$emit("error", false);
            }
        }
    }
</script>

<style scoped>

</style>