<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">Добро пожаловать, Партнёр!</div>
        </div>
        
        
        <div class="popup__content">
            <div class="text-size-l text-bolder">Первым делом создайте страницу «О поставщике»</div>
            
            <div class="mt16">
                Эта страница — небольшая презентация вашей компании, она будет видна каждому покупателю.
            </div>
            
            <div class="mt32">
                <button class="btn btn--white" @click="close(popups.partnerEditAboutWelcome.name)">Создать страницу «О поставщике»</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "PartnerEditAboutWelcome",
        methods: {
            close: function (popup) {
                this.$store.commit('popups/close', {name: popup});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

