<template>
    <div>
        <div @click="errorPopupShow = false;openInput();" v-if="file.icon"
             class="construct-form__logo-add"
             :class="{error:error}"
             :style="{'background-image': 'url(//api.zemskidki.ru/'+file.icon+')'}">
            <span class="w">
                <span class="icon" v-if="!isSend"><span class="i i-photo"></span></span>
                <span v-else class="preloader"></span>
                <span class="t">Загрузить <br> логотип</span>
            </span>
        </div>
        <div v-else @click="errorPopupShow = false;openInput();"
             class="construct-form__logo-add --blank">
            <span class="w" :class="{error:this.error}">
                <span class="icon" v-if="!isSend"><span class="i i-photo"></span></span>
                <span v-else class="preloader"></span>
                <span class="t">Загрузить <br> логотип</span>
            </span>
        </div>
        <input v-if="!isSend" :accept="acceptType" style="display: none;" ref="fileInput" @change="onFileChange" type="file">
        <cropper @crop="onFileCrop"
                 :aspect-ratio="false"
                 @hide="cropperVisible = false"
                 @close="cropperShow = false"
                 :prop-popup-show="cropperShow"
                 v-show="cropperVisible"
                 v-if="targetFile && cropperShow && !errorPopupShow"
                 :type="targetFileType"
                 :src="targetFile"
                 :minCropBoxWidth="minWidth"
                 :minCropBoxHeight="minHeight"
                 :width="targetFileWidth"
                 :height="targetFileHeight"></cropper>
        <popup @close="close()" :show="errorPopupShow" :width="720">
            <div id="popup-cropper-warning">
                <div class="text-size-xl text-color-red text-bolder">
                    Размер изображения должен быть <br>
                    не меньше 800x600px
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>
        <popup @close="close()" :show="errorSizePopupShow" :width="720">
            <div>
                <div class="text-size-xl text-color-red text-bolder">
                    Вес изображения не должен быть <br>
                    более 10Мб
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>
        <popup @close="close()" :show="errorTypePopupShow" :width="720">
            <div>
                <div class="text-size-xl text-color-red text-bolder">
                    Допускаются только форматы изоборажения JPG или PNG
                </div>


                <div class="popup__footer">
                    <a @click="openInput();" href="javascript:;" class="btn">Загрузить другое изображение</a>
                </div>
            </div>
        </popup>
    </div>
</template>

<script>

    import FileUploadCropper from "@/components/forms/FileUploadCropper.vue";
    export default { extends: FileUploadCropper }
</script>

