            <a href="javascript:void(0)" class="l link-icon" @click="openMedia">
                <span class="i i-photo"></span>
                <span class="text">Добавить фото</span>
            </a>
            
            <a href="javascript:void(0)" class="l link-icon" @click="openMedia">
                <span class="i i-video"></span>
                <span class="text">Добавить видео</span>
            </a>
        </div> -->
        
        <div class="wrap-hidden">
            <div class="pt24">
                <span class="i i-photo mr8"></span>
                <span class="t dib text-bold">Фотогалерея <span v-if="nameTitle.length > 0">«{{ nameTitle }}»</span></span>
            </div>
            
            <div class="row-b mt8">
                <div class="row-b__col elem-pos-fit">
                    <file-upload-cropper-gallery-multi
