                            :field-name="name"
                            :move-to-list="moveToList"
                            :is-move="isMove"
                            action-send="partner/sendImageFileGallery"
                            action-delete="false"
                            :btn-text="'Загрузить цветную скан-копию'"
                            :hint-solo="'Минимальный размер — 800х600px, максимальный вес — 10Мб'"
                            :accept-type="'image/jpeg,image/jpg,image/png,image/gif'">
                    </file-upload-cropper-gallery-multi>
                </div>
                
                <div class="row-b__col elem-pos-fix" style="width: 257px; min-width: 257px; ">
                    <div class="mtb">
                        <div class="mt16 text-size-xs text-color-grey">Допускаются форматы JPG или PNG, минимальный<br>размер 800х600px, максимальный вес каждого файла <br>— не более 10Мб</div>
                    </div>
                </div>
            </div>
            <!-- end row-b -->
            
            
            
            <div class="mt32">
                <span class="i i-video mr8"></span>
                <span class="t dib text-bold">Видео <span v-if="nameTitle.length > 0">«{{ nameTitle }}»</span></span>
            </div>
            
            
            
            <div class="row-b mt8">
                <video-gallery-add :value="value.videos"
                                   @input="changeVideo"
                                   class="row-b__col elem-pos-fit"></video-gallery-add>
                <div class="row-b__col elem-pos-fix" style="width: 257px; min-width: 257px; ">
                    <div class="mtb">
                        <div class="mt16 text-size-xs text-color-grey">Чтобы вставить видео, предварительно загрузите <br>его на YouTube и скопируйте ссылку</div>
                    </div>
                </div>
            </div>
            <!-- end row-b -->
        </div>
    </div>
    <!-- end item -->
</template>


<script>
    import Velocity from 'velocity-animate';
    import FileUploadCropperGalleryMulti from "@/components/forms/FileUploadCropperGalleryMulti.vue";
    import VideoGalleryAdd from '@/components/forms/VideoGalleryAdd.vue'

    export default {
        name: "ConstructMediaAdd",
        components: {
            FileUploadCropperGalleryMulti,
            VideoGalleryAdd,
        },
        props: {
            open: {default: false},
            error: {default: false},
            isModeraite: {default: false},
            name: {default: 'image'},
            nameTitle: {default: ''},
            moveToList: {
                default(){
                    return []
                }},
            isMove:  {default: true},
            value: {default() {
                return {
                    videos: [],
                    photos: [],
                }
            }}
        },

        data: function () {
            return {
                opened: false,
            }
        },

        computed: {},

        created: function () {
            //this.$set(this, 'opened', !!this.open);
            //this.$emit('opened', this.opened);
        },

        mounted: function () {

        },

        methods: {
            openMedia(){
                this.opened = !this.opened;
                
                this.$emit('opened', this.opened);
            },

            leaveSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideUp", { duration: 300 }).then(function(e) { done(); });
            },

            changeVideo(val) {
                this.$emit('input', {
                    videos: val,
                    photos: this.value.photos,
                });
                if(this.value.photos.length === 0 && val.length === 0) {
                    //this.openMedia()
                }
            },

            changePhoto(val) {
                this.$emit('input', {
                    videos: this.value.videos,
                    photos: val,
                });
                if(val.length === 0 && this.value.videos.length === 0) {
                    //this.openMedia()
                }
            },
            movePhoto(val) {
                this.$emit('move', val);
            },

        }
    }
</script>

